.contacts {
&__form {
  margin-bottom: 48px;

  @include onTabletPlus {
    margin-bottom: 0;
  }
}

&__info {
  margin-bottom: 24px;
}

  &__title {
    color: $gray-contacts;
    margin-bottom: 8px;
  }

  &__link {
    text-decoration: none;
    color: $text-black;

    &:hover {
      color: $gray-contacts;
    }
  }

  &__form-field {
    margin-bottom: 16px;

    &--last {
      margin-bottom: 40px;
    }
  }
}
