.form-field {
  box-sizing: border-box;
  height: 48px;
  width: 100%;

  padding: 14px 16px;
  border: 1px solid transparent;
  outline: none;

  background-color: #f8f8fA;
  border-radius: 40px;

  line-height: 143%;

  &--textarea {
    height: 148px;
    resize: none;
    font-family: inherit;
    border-radius: 16px;
  }

  &::placeholder {
    color: #7e7e83;
    font-size: 14px;
  }

  &:hover {
    border-color: #d5d7dE;
  }

  &:focus {
    border-color: $text-black;
  }
}
